@-webkit-keyframes slide {
  0% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: var(--origin);
            transform-origin: var(--origin);
    opacity: 0;
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: var(--origin);
            transform-origin: var(--origin);
    opacity: 1;
  }
}

@keyframes slide {
  0% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: var(--origin);
            transform-origin: var(--origin);
    opacity: 0;
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: var(--origin);
            transform-origin: var(--origin);
    opacity: 1;
  }
}

