.wrapper {
  margin: 0 auto;
  width: 330px;
  font-family: "Helvetica";
  font-size: 14px;
  border: 1px solid #ccc;
  padding: 20px;
}
.step-progress {
  position: relative;
  padding-left: 45px;
  list-style: none;
}
.step-progress::before {
  display: inline-block;
  content: "";
  position: absolute;
  top: 0;
  left: 24px;
  width: 10px;
  height: 100%;
  border-left: 2px solid #ccc;
}
.step-progress-item {
  position: relative;
  counter-increment: list;
  padding-top: 10px;
  padding-left: 15px;
}
.step-progress-item:not(:last-child) {
  padding-bottom: 50px;
}
.step-progress-item::before {
  display: inline-block;
  content: "";
  position: absolute;
  left: -21px;
  height: 100%;
  width: 10px;
}
.step-progress-item::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  left: -38px;
  width: 32px;
  height: 32px;
  border: 2px solid #ccc;
  border-radius: 50%;
  background-color: #fff;
}
.step-progress-item.is-done::before {
  border-left: 2px solid #1a3d7c;
}
.step-progress-item.is-done::after {
  background-image: url(data:image/svg+xml;base64,);
  font-size: 20px;
  color: #fff;
  text-align: center;
  border: 2px solid #1a3d7c;
}

.step-progress-item.current::before {
  border-left: 2px solid #1a3d7c;
}
.step-progress-item.current::after {
  background-image: url(data:image/svg+xml;base64,);
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: center;
  width: 32px;
  height: 32px;
  top: 0;
  left: -38px;
  font-size: 14px;
  text-align: center;
  color: #1a3d7c;
  border: 2px solid #1a3d7c;
  background-color: white;
}
.step-progress strong {
  display: block;
}

.header {
  color: white;
}
